import "core-js/modules/es.array.push.js";
import { reactive, toRefs, onMounted } from 'vue';
import Header from '../store/components/Header.vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
export default {
  components: {
    Header
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    const data = reactive({
      list: []
    });
    const onAdd = () => {
      router.push({
        path: '/addressEdit',
        query: {
          type: 'add'
        }
      });
    };
    const onEdit = item => {
      router.push({
        path: '/addressEdit',
        query: {
          id: item.id,
          type: 'edit'
        }
      });
    };

    // 初始化地址
    const init = () => {
      data.list = store.state.userAddress.map(item => {
        return {
          id: item.id,
          name: item.name,
          tel: item.tel,
          address: `${item.province}${item.city}${item.county}${item.addressDetail}`,
          isDefault: item.isDefault
        };
      });
    };
    onMounted(() => {
      init();
    });
    return {
      ...toRefs(data),
      onAdd,
      onEdit
    };
  }
};